@import url("https://cdn.jsdelivr.net/npm/bootstrap-icons@1.8.3/font/bootstrap-icons.css");

.navBlock{
  color:#232150;
  height: 12vw;
  position: sticky;
  top: 0;
  z-index: 1;
  background-color: white;
}

.smallNavBar{
  position: sticky;
  top:0%;
  background-color: white;
  z-index: 1;
}

.NavLink{
  padding: 15px;
  font-size: 2rem;
}

.NavLink:hover{
  color: #c63182;
}

.largeScreenLogo{
  width: 70%;
}

.smallMenuButtons{
  width: 100px;
  height: 100px;
}

.offcanvas-body{
  background-color: black;
}

.headerImage{
  max-width: 80%;
  max-height: 60%;
  position: relative;
  left: 10%;
  top: 20%;
}


.blob{
  position: relative;
  width: 100%;
  z-index: -1;
  left: -5%;
  top: -80%;
}

.generalParagraph{
  font-size: 2rem;
}

.doCard{
  background: rgba( 52, 51, 51, 0.45 );
box-shadow: 0 8px 32px 0 rgba( 31, 38, 135, 0.37 );
backdrop-filter: blur( 20px );
-webkit-backdrop-filter: blur( 20px );
border-radius: 10px;
border: 1px solid rgba( 255, 255, 255, 0.18 );
}

.littleBit{
  margin-top: 15%;
}

.btnDiv{
  margin-top: 12%;
}
.techImages{
  border: solid 2px black;
  margin: 50px;
  max-width: 100%;
}




.card-text{
  min-height: 200px;
}
.remark{
  color: #1DB954;
  font-weight: bold;
}




.footer{
  background-color: #232150;
}

.bi{
  font-size: 4rem;
  margin: 20px;
}

.bi:hover{
  font-size: 4.5rem;
}

.footerHeader{
  font-size: 2rem;
}

.footerText{
  font-size: 2rem;
}

.footerLink:hover{
  text-decoration: underline;
}

.ContactForm{
  border: solid 3px gray;
}

a {
  text-decoration: none;
  color: inherit;
}

a:hover{
  font-size: 2rem;
  color: inherit;
}


 .SocialLogo{
  width: 4rem;
  background-color: aliceblue;
 }

 .SocialLogo:hover{
  width: 4.5rem;
 }

.subtitle{
  color: gray;
}

.subtitle2{
  color: aliceblue;
}

.card-img-top{
  width: 80%;
}

.card-top{
  height: 250px
}


.card{
  width: 32%;
  border: solid 2px black;
  border-radius: 10px;
  background: rgba( 255, 255, 255, 0.25 );
  box-shadow: 0 8px 32px 0 rgba( 31, 38, 135, 0.37 );
  backdrop-filter: blur( 4px );
  -webkit-backdrop-filter: blur( 4px );
  border-radius: 10px;
  border: 1px solid rgba( 255, 255, 255, 0.18 );
}




.projectText{
  padding: 15px;
  height: fit-content;
  background: rgba( 255, 255, 255, 0.05 );
  box-shadow: 0 8px 32px 0 rgba( 31, 38, 135, 0.37 );
  backdrop-filter: blur( 4px );
  -webkit-backdrop-filter: blur( 4px );
  border: 1px solid rgba( 255, 255, 255, 0.18 );
  font-size: 105%;
}


.imgCard{
  max-height: 500px;
}

.projectImg{
  transition: all .2s ease-in-out; 
  max-height: 100%;
}

.hamburger{
  width: 35%;
  height: 35%;
}



.projectImg:hover{
  /* Start the shake animation and make the animation last for 0.5 seconds */
  transform: scale(1.1); 
}




.projectCardMain{
  border: solid 2px black;
  padding: 50px;
  background: rgba( 44, 39, 39, 0.9 );
  box-shadow: 0 8px 32px 0 rgba( 31, 38, 135, 0.5 );
  backdrop-filter: blur( 0px );
  -webkit-backdrop-filter: blur( 0px );
  border-radius: 10px;
}


.img-Card{
  width: 100%;
  height: 25em;
}
.header2{
  font-size: 3rem;
  color: #232150;
  font-family: 'Roboto', 'sans-serif';
  text-shadow: -4px 0px #1DB954;
  padding: 0%;
  margin: 0%;
}
.justify{
  text-align: justify;
}

.subtitle{
  font-size: 40px;
}

.article{
  max-width: 100%;
}


.servicesCardMain{
  background: rgba( 255, 255, 255, 0.25 );
  width: 33%;
box-shadow: 0 8px 32px 0 rgba( 31, 38, 135, 0.37 );
backdrop-filter: blur( 4px );
-webkit-backdrop-filter: blur( 4px );
border-radius: 10px;
border: 1px solid rgba( 255, 255, 255, 0.18 );
}


.userMessage{
  width: 100%;
  height: 70%;
}

.note{
  border: solid 1px black;
  padding: 50px;
  background-color: beige;
}
